'br'),_vm._v("\n          儘管我事前對西打不太熟悉，但對談中西打就好像既陌生又親切的前輩😂可以很放心的告訴西打自己的困境！我認為這是在諮詢過程最重要的，是西打本身個人特質帶給人的信賴感！能夠很放心得把問題交給他處理！"),_c('br'),_c('br'),_vm._v("\n\n          我其中一直困擾自己許久就是「到底接案到一定程度的時候，到底要如何拓展自己的事業？」西打給我的解答是「規模化自己」😂也讓我蠻驚訝原來我算是還蠻十項全能類型的；但這也是我現在擴大自己的最大痛點。"),_c('br'),_vm._v("\n          還有很多很多包含一人公司營運分享、合作夥伴分享等、規模化、角色切換等；由於身邊朋友都是社畜😂能有西打可以跟我分享創業的大小經驗實在讓我非常受用！\n        ")])]),_vm._v(" "),_c('div',{staticClass:"case-student-box"},[_c('img',{directives:[{name:"lazy",rawName:"v-lazy",value:(require('@/assets/img/case/user.png')),expression:"require('@/assets/img/case/user.png')"}],staticClass:"case-student-icon",attrs:{"alt":"siddharam"}}),_vm._v(" "),_c('div',{staticClass:"case-student-title"},[_vm._v("資深網站設計師"),_c('br'),_vm._v("（5500+ IG 粉絲）")]),_vm._v(" "),_c('div',{staticClass:"case-student-text"},[_vm._v("\n          因為西打的自由工作者資歷滿豐富的，所以給的回饋已經不是從「執行端」的接案者出發，而是從更高視角的「策略端」出發，跟預期收到的回饋方向很不一樣，但也打開了我的視角，讓我能少走很多彎路。過程中，西打也會一個接著一個議題討論，讓你有思考和消化的空間，所以不會感覺資訊量太大。"),_c('br'),_vm._v("\n          我的很多擔憂跟不確定性都停留於「員工思維」，例如我會擔心成為自由工作者後，設計能力就下降了，但我沒有想到的是還有好多可以培養的能力，例如「行銷、業務、談判、外包、會計」等等，需要一段時間轉換成「老闆思維」，也要再探索自己是不是真的想長出這些能力。"),_c('br'),_vm._v("\n          其實我原本也有點擔心我會不會沒什麼收穫，但發現更有收穫耶，因為我沒有你那樣的經歷，自然不會培養出那樣的思維。\n        ")])]),_vm._v(" "),_c('div',{staticClass:"case-student-box"},[_c('img',{directives:[{name:"lazy",rawName:"v-lazy",value:(require('@/assets/img/case/user.png')),expression:"require('@/assets/img/case/user.png')"}],staticClass:"case-student-icon",attrs:{"alt":"siddharam"}}),_vm._v(" "),_c('div',{staticClass:"case-student-title"},[_vm._v("專案經理"),_c('br'),_vm._v("身心靈平台創業者")]),_vm._v(" "),_c('div',{staticClass:"case-student-text"},[_vm._v("\n          西打對於問題的歸納能力很強，能根據我的問題提供對應方法。"),_c('br'),_vm._v("\n          過程中，能根據我現在狀況，提供給我合適的選項。即使這個狀況，西打不一定遇過，也能明瞭我的處境。"),_c('br'),_c('br'),_vm._v("\n\n          此外也能夠針對這個選項，拆解出幾個小步驟，這部份真的滿喜歡的，讓我更知道怎麼實行。"),_c('br'),_vm._v("\n          結束前也會跟我一起歸納重點，還有 Action item，不會讓整個諮詢過於發散，或不知道下一步怎麼走。我覺得這是諮詢中很難得可貴的過程。"),_c('br'),_c('br'),_vm._v("\n\n          收穫比想像中的”快”，很意外在前幾分鐘的時候，能被指引出較可行的方向。"),_c('br'),_vm._v("\n          這對於我來說是滿驚訝的，我原本覺得我的問題，可能不好回答，需要來回確認才能弄清楚。\n        ")])])])]),_vm._v(" "),_c('div',{staticClass:"case-pain"},[_c('div',{staticClass:"case-pain-title"},[_vm._v("你正在經歷"),_c('br'),_vm._v("這些商業煩惱嗎？")]),_vm._v(" "),_c('div',{staticClass:"case-pain-outer"},[_c('div',{staticClass:"case-pain-box"},[_c('img',{directives:[{name:"lazy",rawName:"v-lazy",value:(require('@/assets/img/case/difficulties.png')),expression:"require('@/assets/img/case/difficulties.png')"}],staticClass:"case-pain-icon",attrs:{"alt":"siddharam"}}),_vm._v(" "),_c('div',{staticClass:"case-pain-text"},[_vm._v("很努力了"),_c('br'),_vm._v("但找不到付費客戶")])]),_vm._v(" "),_c('div',{staticClass:"case-pain-box"},[_c('img',{directives:[{name:"lazy",rawName:"v-lazy",value:(require('@/assets/img/case/unbalanced.png')),expression:"require('@/assets/img/case/unbalanced.png')"}],staticClass:"case-pain-icon",attrs:{"alt":"siddharam"}}),_vm._v(" "),_c('div',{staticClass:"case-pain-text"},[_vm._v("列好商業方案"),_c('br'),_vm._v("但不確定對不對")])]),_vm._v(" "),_c('div',{staticClass:"case-pain-box"},[_c('img',{directives:[{name:"lazy",rawName:"v-lazy",value:(require('@/assets/img/case/no-talking.png')),expression:"require('@/assets/img/case/no-talking.png')"}],staticClass:"case-pain-icon",attrs:{"alt":"siddharam"}}),_vm._v(" "),_c('div',{staticClass:"case-pain-text"},[_vm._v("不太會談業務"),_c('br'),_vm._v("常常沒有後續消息")])])])]),_vm._v(" "),_c('div',{staticClass:"case-me"},[_c('div',{staticClass:"case-me-inner"},[_c('div',{staticClass:"case-me-title"},[_vm._v("我是誰")]),_vm._v(" "),_c('div',{staticClass:"case-me-text"},[_vm._v("真實工作與接案歷程")]),_vm._v(" "),_c('img',{directives:[{name:"lazy",rawName:"v-lazy",value:(require('@/assets/img/me/logo.webp')),expression:"require('@/assets/img/me/logo.webp')"}],staticClass:"case-me-logo",attrs:{"alt":"siddharam"}})]),_vm._v(" "),_c('div',{staticClass:"case-me-inner2"},[_c('div',{staticClass:"case-me-box"},[_c('div',{staticClass:"case-me-desc"},[_vm._v("\n          我是西打，今年 32 歲，曾任職 3 年記者、1.5 年軟體工程師。於 2O2O.2 離開職場，全職接案至今。"),_c('br'),_c('br'),_vm._v("\n\n        同年，我成立「西打藍」個人品牌，每週寫 2 篇接案歷程文，記錄每週接案感悟。當前 IG 追蹤破萬，電子報 2600 名以上訂閱。"),_c('br'),_c('br'),_vm._v("\n\n        接案這些年，我以網站建置、文字服務為主要服務。漸漸的，我掌握接案技巧，幾乎沒有再主動開發，案源就主動進來。"),_c('br'),_c('br'),_vm._v("\n\n        期間，我代筆出版過 3 本書：《修煉——全民服務的數位轉型》（商周出版）、《看海記事：基隆港職人生活誌》（蔚藍文化）、《望基隆：船行年代的港城舊事》（蔚藍文化）。"),_c('br'),_c('br'),_vm._v("\n\n        2022 年，我的收入扣除工作支出後，超過百萬元。2023 年，我開始合夥、小規模創業，有做日本運動防護用品總代理生意、前端程式語言教學小班、「夜讀時光」付費閱讀社群，也和夥伴在基隆防空洞策展，4 天參訪數超過 5000 人，嘗試各種創業可能。"),_c('br'),_c('br'),_vm._v("\n\n        2024 年，我成立「西打藍好內容有限公司」，承接網站、文字、品牌設計案，並在第一季就談妥一整年的案源，也成功申請過創業補助。"),_c('br'),_c('br'),_vm._v("\n\n        我目前報價最高的案例，有成功談成 94.5 萬的合作案。"),_c('br'),_c('br'),_vm._v("\n\n        而今，我也擔任 3 間公司的商業行銷顧問，並曾幫助 90 多位付費學員順利接案。同時，我也正合夥一間原創傢俱的經銷代理公司。"),_c('br'),_c('br'),_vm._v("\n\n        我希望透過這次的「年度顧問專約」，分享我這些年的接案、創業心法，給正走在同樣路上的你。以下是我這些年的重要經歷："),_c('br'),_c('br')])])])]),_vm._v(" "),_c('div',{staticClass:"case-experience"},[_c('div',{staticClass:"case-experience-box"},[_c('img',{directives:[{name:"lazy",rawName:"v-lazy",value:(require('@/assets/img/case/computer.png')),expression:"require('@/assets/img/case/computer.png')"}],staticClass:"case-experience-icon",attrs:{"alt":"siddharam"}}),_vm._v(" "),_c('div',{staticClass:"case-experience-desc"},[_vm._v("5 年全職接案、創業經驗，幾乎不宣傳，案源卻不斷，也嘗試過數十種收入源，最高合作案價 94.5 萬")])]),_vm._v(" "),_c('div',{staticClass:"case-experience-box"},[_c('img',{directives:[{name:"lazy",rawName:"v-lazy",value:(require('@/assets/img/case/conversation.png')),expression:"require('@/assets/img/case/conversation.png')"}],staticClass:"case-experience-icon",attrs:{"alt":"siddharam"}}),_vm._v(" "),_c('div',{staticClass:"case-experience-desc"},[_vm._v("有超過 95 位上班族、接案者、創業家付費找我 1v1 諮詢，客戶回饋一字不減全公開，滿意度極高")])]),_vm._v(" "),_c('div',{staticClass:"case-experience-box"},[_c('img',{directives:[{name:"lazy",rawName:"v-lazy",value:(require('@/assets/img/case/stack-of-books.png')),expression:"requir